import { Howl } from "howler";

const CORS_PROXY = "https://try.readme.io/";

  const uberduckKey = "pub_fgccbarfordqaiulif";
  const uberduckSecret = "pk_a5a7941f-11db-4cee-a2f8-ef68fcf2e4a6";


  const uberduck = (voice, speech) => {
    if (!voice) return;
    fetch(`${CORS_PROXY}https://api.uberduck.ai/speak-synchronous`, {
      method: "POST",
      body: JSON.stringify({ speech, voice }),
      headers: {
        Authorization: `Basic ${btoa(uberduckKey + ":" + uberduckSecret)}`,
      },
    })
      .then((res) => res.blob())
      .then((blob) => {
        const url = window.URL.createObjectURL(blob);
        const sound = new Howl({
          src: [url],
          format: ["wav"],
        });
        sound.play();
      })
      .catch();
  };

  export default uberduck;